
.feedback__textarea {
  width: 279px;
  min-height: 150px;
  margin-bottom: 56px;
  margin-left: 20px;
  font-family: inherit;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  border: none;
  background: $grey;
  padding: 16px;
  text-align: left;
  resize: none;
  margin-top: 20px;

  @media (min-width: $tablet-width) {
    width: 648px;
    margin-left: 60px;
    margin-bottom: 48px;
  }

  @media (min-width: $desktop-width) {
    width: 768px;
    margin-left: 216px;
  }
}
