.video__about {
  padding-left: 28px;
  padding-right: 26px;
  padding-bottom: 38px;
  text-align: center;

  br {
    display: none;
  }

  @media (min-width: $tablet-width) {
    padding-bottom: 58px;
    padding-top: 58px;

    br {
      display: block;
    }
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 62px;
  }
}

.video__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
}

.video__description {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
}

.video__card {
  padding-left: 20px;
  padding-right: 20px;
}

.video__presentation {
  @media (min-width: $tablet-width) {
    width: 100%;
  }

  @media (min-width: $desktop-width) {
    width: 100%;
  }
}
