.main-nav__list {
  margin: 0;
  padding: 0;
  color: inherit;
  text-decoration: none;
  list-style-type: none;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    width: 90%;
    max-width: 960px;
    margin: 0 auto;
    padding: 0;
  }
}

.main-nav__list::after {
  content: "";
  grid-column: 3/4;
  grid-row: 1/3;
}

.main-nav {
  position: absolute;
  width: 100%;
  z-index: 1;
}

.main-nav--opened .main-header__burger,
.main-nav--opened .main-header__burger::before,
.main-nav--opened .main-header__burger::after {
  background: #ffffff;
}

.main-nav--opened .main-header__burger {
  background: transparent;
}

.main-nav--opened .main-header__burger::before {
  top: 50%;
  transform: rotate(45deg);
}

.main-nav--opened .main-header__burger::after {
  top: 50%;
  transform: rotate(-45deg);
}

.main-nav--closed .main-header__toggle:active::before {
  background-color: fade(#81b3d2, 30%);
  box-shadow: 0 5px 0 0 fade(#81b3d2, 30%), 0 14px 0 0 fade(#81b3d2, 30%);
}

.main-nav--closed .main-nav__list {
  @media (max-width: $mobile-width-only) {
    display: none;
  }
}
